import { IFrameReactContainer, useTranslation } from "@fanfanlo";
import { Box, Button, Stack, TextareaAutosize, Typography } from "@mui/material";
import { ScriptEditorContextLoader } from "@src/components/script-editor/data/context";
import { loadScrptEditorData } from "@src/components/script-editor/data/script-editor.data.intf";   
import { marketHtmlUtils } from "@src/markets/market-html-utils";
import { useState } from "react";

export function ScriptEditorContent({ns}:{ns?:string}) {

    const [script, setScript] = useState(scriptMsg);
    const [runScript, setRunScript] = useState('')
    const { t } = useTranslation(`homepage/components/script-editor/content/content`)
    function onRunClick(){
        setRunScript('')
        setTimeout(()=>setRunScript(script), 100 * 3)
    }
    return (
        <>
        <ScriptEditorContextLoader value={loadScrptEditorData(ns)}>
            <Box>
                <Typography variant="h5">{t(ns ? 'ScriptEditorContent.name.editScript' : 'ScriptEditorContent.name.createScript')}</Typography>
                <Stack direction={"column"} spacing={2}>
                    <Box>
                        <Button>examples2</Button>
                        <Button>docs</Button>
                        <Button onClick={onRunClick}>run</Button>
                    </Box>
                    <Box>
                        <TextareaAutosize
                        value={script}
                        onChange={(e)=>setScript(e.target.value)}
                         style={{width:"100%",minHeight:"200px"}} />
                    </Box>
                    <Box></Box>
                    <Box>
                        {runScript && <IFrameReactContainer
                        content={runScript}
                        urlAsContent={true}
                        htmlBuilder={marketHtmlUtils.mergeHtml}
                        />}
                    </Box>
                </Stack>
            </Box>
        </ScriptEditorContextLoader>
        </>
    )
}

