import { createAsyncContextLoader, createTypedContext } from "@fanfanlo";
import { IScriptEditorContentData } from "./script-editor.data.intf";
import { Fallback } from "@src/components/fallback/Fallback";


export const [ScriptEditorContextProvider, useScriptEditorContext] = createTypedContext<IScriptEditorContentData>();

export const ScriptEditorContextLoader = createAsyncContextLoader({
    Provider: ScriptEditorContextProvider,
    extract: (res: IScriptEditorContentData) => res,
    fallback: <Fallback />,
});