import { loadI18nValue, storeUtils } from "@fanfanlo";
import { IScriptData } from "@market/lib";
import _ from "lodash";
import { ulid } from "ulid";

const ns = "script-editor";
const androidData = `${ns}:android`;
const webData = `${ns}:web`;
function createScriptNs(id:string){
    return `${androidData}:${id}`
}
export const scriptEditorNs = {
    ns,
    androidData,
    webData,
}

export interface IScriptEditorData{
    script:IScriptData
}
export interface IScriptEditorContentInfo{
    dataModified:boolean
    htmlPreviewIsShow:boolean
}
export interface IScriptEditorContentData{
    data:IScriptEditorData
    info:IScriptEditorContentInfo
}

async function createDefaultScriptEditorData():Promise<IScriptEditorContentData>{
    const unnamed = await loadI18nValue(`homepage/components/script-editor/data/content`, 'content.unnamed')
    const id = ulid()
    return {
        info:{
            dataModified:false,
            htmlPreviewIsShow:false
        },
        data:{script:{
            ns:createScriptNs(id),
            name:unnamed,
            version:"1.0.0",
            url:"/script-editor",
            content:"",
            id:id
        }}
    }
}

export async function createScriptEditorData(data?:Partial<IScriptEditorData>):Promise<IScriptEditorContentData>{
    const def = await createDefaultScriptEditorData();
    const res = _.merge(def,data);
    return res;
}

export async function loadScrptEditorData(ns?:string):Promise<IScriptEditorContentData>{
    const def = await createDefaultScriptEditorData()
    ns = ns || def.data.script.ns
    const s = await storeUtils.getNamespaceStoreWithData(ns, {forceReset:false, mergeDefData:true, defData:def})
    return s.data
}